import React, { useState, useRef }  from "react";
import { withRouter }       from "react-router-dom";
import { connect }          from "react-redux";
import { makeStyles }       from "@material-ui/styles";
import { Loader }           from "components";
import { newUser }          from "actions/user";
import * as Utils           from "helpers/utils";

import ArrowBackIcon        from "@material-ui/icons/ArrowBack";
import UserData             from "./UserData";
import UserAddress          from "./UserAddress";
import {
    Typography,
    IconButton,
    Grid,
} from "@material-ui/core";

const NewAccount = ({ history, newUser }) => {
    const classes = useStyles();
    const rootRef = useRef();
    const [ index, setIndex ] = useState(0);
    const [ loader, updateLoader ] = useState(false);
    const [ userData, _setUserData ] = useState(null);

    const handleBack = () => {
        if (loader)
            return null;

        if (index === 0)
            return history.goBack();

        return setIndex(index - 1);
    }

    const formatData = _data => {
        let data = {};

        for (let field in _data)
            data[field] = _data[field].value;

        return data;
    }

    // setting data
    const setUserData = data => {
        _setUserData( formatData(data) );
        return setIndex(index + 1);
    }

    const setUserAddress = data => {
        updateLoader(true);

        return newUser({
            ...userData,
            address: formatData(data),
        }, status => {
            updateLoader(false);

            if (status)
                return window.location.href = "/app";

            return Utils.notificationError("Não foi possível criar sua conta nesse momento! Verifique sua internet, e tente novamente!");
        })
    }

    return (
        <div className={classes.root} ref={ rootRef }>
            <Grid className={classes.grid} container>
                <Grid className={classes.quoteContainer} item lg={5}>
                    <div className={classes.quote}>
                        <div className={classes.quoteInner}>
                            <Typography className={classes.quoteText} variant="h1">Dar as mãos por uma boa causa:<br/>esse é o nosso jogo!</Typography>
                            <Typography className={classes.bio} variant="body2">(No sentido figurado, nada de contato físico)</Typography>
                            <Typography className={classes.name} variant="body1">Autor Desconhecido</Typography>
                        </div>
                    </div>
                </Grid>
                <Grid className={classes.content} item lg={7} xs={12}>
                    <div className={classes.content}>
                        <div className={classes.contentHeader}>
                          <IconButton onClick={ handleBack }>
                              <ArrowBackIcon />
                          </IconButton>
                        </div>
                        <div className={classes.contentBody}>
                            <UserData onUserDataConcluded={ setUserData } show={ index === 0 }/>
                            <UserAddress onUserAddressConcluded={ setUserAddress } show={ index === 1 && loader === false }/>
                            <Loader style={{ width: "100%" }} active={ loader }/>
                        </div>
                    </div>
                </Grid>
            </Grid>
        </div>
    )
}

export default connect(null, { newUser })(withRouter(NewAccount));

const useStyles = makeStyles(theme => ({
    root: {
        backgroundColor: theme.palette.background.default,
        height: "100%"
    },
    grid: {
        height: "100%"
    },
    quoteContainer: {
        [theme.breakpoints.down("md")]: {
            display: "none"
        }
    },
    quote: {
        backgroundColor: theme.palette.neutral,
        height: "100%",
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
        backgroundImage: `url(${process.env.PUBLIC_URL}/auth.jpg)`,
        backgroundSize: "cover",
        backgroundRepeat: "no-repeat",
        backgroundPosition: "center"
    },
    quoteInner: {
        textAlign: "center",
        flexBasis: "600px"
    },
    quoteText: {
        color: theme.palette.white,
        fontWeight: 300
    },
    name: {
        marginTop: theme.spacing(1),
        color: theme.palette.white
    },
    bio: {
        color: theme.palette.white,
        marginTop: 5
    },
    content: {
        height: "100%",
        display: "flex",
        flexDirection: "column",
        overflowY: "auto"
    },
    contentHeader: {
        display: "flex",
        alignItems: "center",
        paddingTop: theme.spacing(2),
        paddingBototm: theme.spacing(2),
        paddingLeft: theme.spacing(2),
        paddingRight: theme.spacing(2)
    },
    contentBody: {
        flexGrow: 1,
        display: "flex",
        alignItems: "center",
        [theme.breakpoints.down("md")]: {
            justifyContent: "center"
        }
    }
}));
