import React            from "react";
import { makeStyles }   from "@material-ui/styles";
import * as Utils       from "helpers/utils";
import {
    Typography,
    Button,
    Grid,
} from "@material-ui/core";

const NotificationPermission = () => {
    const classes = useStyles();
    const onClick = () => {
        Utils.requestPermissionForNotitifications()
        Utils.closeModal();
    }

    return (
        <div className={ classes.paper }>
            <Grid container spacing={ 4 }>
                <Grid item xs={ 12 } style={{ marginBottom: -15 }}>
                    <Typography variant="h4">O que acha de receber notificações em tempo real sobre suas ajudas por aqui?</Typography>
                    <Typography variant="subtitle2" style={{ margin: "10px 0 0" }}>Clique no botão abaixo para autorizar o recebimento das notificações</Typography>
                </Grid>
                <Grid item xs={ 12 }>
                    <Button fullWidth onClick={ onClick } color="primary" variant="contained">Autorizar Notificações Push</Button>
                </Grid>
            </Grid>
        </div>
    )
}

export default NotificationPermission;

const useStyles = makeStyles((theme) => ({
    paper: {
        backgroundColor: theme.palette.background.paper,
        boxShadow: theme.shadows[5],
        padding: theme.spacing(4),
        borderRadius: 10,
        minWidth: 350,

        [theme.breakpoints.down("xs")]: {
            maxWidth: "90%",
        },
        [theme.breakpoints.up("sm")]: {
            maxWidth: 500,
        },
        [theme.breakpoints.up("md")]: {
            maxWidth: 500,
        },
        [theme.breakpoints.up("lg")]: {
            maxWidth: 450,
        },
    },
}));
