import React                from "react";
import * as Pages           from "pages";
import { Switch, Redirect } from "react-router-dom";
import { Minimal }          from "layouts";
import * as Modals          from "modals";
import { store }            from "reducers/store";
import {
    RouteWithLayout,
    Alert
} from "components";

const Routes = () => {
    const { user } = store.getState().user;

    return (
        <>
            <Modals.Modal>
                <Modals.NotificationPermission key="NotificationPermission"/>
                <Modals.ViewProfile key="ViewProfile"/>
                <Modals.HelpDetails key="HelpDetails"/>
                <Modals.RequestHelp key="RequestHelp"/>
                <Modals.ReportUser key="ReportUser"/>
            </Modals.Modal>
            <Switch>
                <Redirect exact from="/" to={ user.name ? "/home" : "/login" }/>

                <RouteWithLayout exact path="/home" component={ Pages.Home } />
                <RouteWithLayout exact path="/about" component={ Pages.About } />
                <RouteWithLayout exact path="/logout" component={ Pages.Logout } />
                <RouteWithLayout exact path="/helps" component={ Pages.RequestedHelps } />
                <RouteWithLayout exact path="/search" component={ Pages.SearchVolunteer } />
                <RouteWithLayout exact path="/volunteer" component={ Pages.BecomeVolunteer } />
                <RouteWithLayout exact path="/my-profile" component={ Pages.EditProfile } />
                <RouteWithLayout exact path="/received-helps" component={ Pages.ReceivedHelps } />

                <RouteWithLayout exact path="/login" component={ Pages.Login } layout={ Minimal } />
                <RouteWithLayout exact path="/new-account" component={ Pages.NewAccount } layout={ Minimal } />
                <RouteWithLayout exact path="/not-found" component={ Pages.NotFound } layout={ Minimal } />
                <Redirect exact from="*" to="/not-found" />
            </Switch>
            <Alert/>
        </>
    );
}

export default Routes;
