import React, { useState }      from "react";
import { Link as RouterLink }   from "react-router-dom";
import * as Utils               from "helpers/utils";
import { makeStyles }           from "@material-ui/styles";
import Input                    from "./Input";
import {
    FormHelperText,
    Typography,
    TextField,
    Checkbox,
    Button,
    Link,
} from "@material-ui/core";

const defaultValue = {
    value: "",
    error: false,
}

const UserData = ({ onUserDataConcluded, show }) => {
    const classes = useStyles();
    const [ isValid, updateIsValid ] = useState(true);
    const [ fields, updateFields ] = useState({
        name: defaultValue,
        cpf: defaultValue,
        phone: defaultValue,
        birthdate: defaultValue,
        email: defaultValue,
        password: defaultValue,
        sex: {
            ...defaultValue,
            value: "m"
        },
        policy: {
            ...defaultValue,
            value: false
        }
    });

    const updateSpecificField = (field, data) => updateFields({
        ...fields,
        [field]: {
            ...fields[field],
            ...data
        }
    });
    const handleChange = ({ target: { name, type, checked, value: v }}, mask) => {
        const value = mask ? Utils.mask(v, mask) : v;

        updateIsValid(true);

        if (name === "policy")
            return updateSpecificField(name, { value: checked, error: null });

        return updateSpecificField(name, { value, error: null });
    };

    const onSubmit = event => {
        event.preventDefault();

        const fieldsLength = {
            cpf: 14,
            phone: 14,
            birthdate: 10,
        }

        for (let field in fields) {
            let { value } = fields[field];

            if (value === "" || value === false || (fieldsLength[field] && value.length !== fieldsLength[field])) {
                updateIsValid(false);
                return updateSpecificField(field, { error: "Preencha o campo corretamente" });
            }
        }

        if (fields.password.length < 6)
            return updateSpecificField("password", { error: "A senha deve ter pelo menos 6 caracteres" });

        if (Utils.validateEmail(fields.email.value) === false)
            return updateSpecificField("email", { error: "Preencha um email válido" });

        return onUserDataConcluded(fields);
    }

    return (
        <form className={`${classes.form} ${ show ? classes.show : classes.hide }`} onSubmit={ onSubmit }>
            <Typography className={classes.title} variant="h2">Criar conta</Typography>
            <Typography color="textSecondary" gutterBottom>Use seu email para criar uma conta</Typography>
            <Input
                field="name"
                label="Nome completo"
                fields={ fields }
                onChange={ handleChange }
            />
            <Input
                field="cpf"
                label="CPF"
                mask="###.###.###-##"
                fields={ fields }
                onChange={ handleChange }
            />
            <Input
                mask="(##)#####-####"
                field="phone"
                label="Telefone"
                fields={ fields }
                onChange={ handleChange }
                type="tel"
            />
            <Input
                mask="##/##/####"
                field="birthdate"
                label="Data de Nascimento"
                fields={ fields }
                onChange={ handleChange }
                type="tel"
            />
            <Input
                field="email"
                label="Email"
                fields={ fields }
                onChange={ handleChange }
                type="email"
            />
            <Input
                field="password"
                label="Senha"
                fields={ fields }
                onChange={ handleChange }
                type="password"
            />

            <TextField
                fullWidth
                label=""
                margin="dense"
                name="sex"
                onChange={ handleChange }
                select
                // eslint-disable-next-line react/jsx-sort-props
                SelectProps={{ native: true }}
                variant="outlined"
                className={ classes.select }
            >
                <option value="m">Homem</option>
                <option value="f">Mulher</option>
            </TextField>

            <div className={classes.policy}>
                <Checkbox
                    checked={ fields.policy.value }
                    className={ classes.policyCheckbox }
                    color="primary"
                    name="policy"
                    onChange={ handleChange }
                />
                <Typography className={ classes.policyText } color="textSecondary" variant="body1">
                    {"Eu aceito os "}
                    <Link
                        color="primary"
                        component={ RouterLink }
                        to="https://quarentena-solidaria.com/TERMOS%20DE%20USO.pdf"
                        underline="always"
                        variant="h6"
                    >
                        Termos e Condições
                    </Link>
                </Typography>
            </div>
            {(fields.policy.error) && (
                <FormHelperText error>{ fields.policy.error }</FormHelperText>
            )}
            <Button
                className={classes.signUpButton}
                color="primary"
                disabled={ !isValid }
                fullWidth
                size="large"
                type="submit"
                variant="contained"
            >
                Criar conta
            </Button>
            <Typography color="textSecondary" variant="body1">
                {"Já possui conta? "}
                <Link component={ RouterLink } to="/login" variant="h6">Faça login</Link>
            </Typography>
        </form>
    )
}

export default UserData;

const useStyles = makeStyles(theme => ({
    select: {
        marginTop: theme.spacing(2),
        minWidth: 120,
        width: "100%"
    },
    content: {
        height: "100%",
        display: "flex",
        flexDirection: "column",
        overflowY: "auto"
    },
    contentHeader: {
        display: "flex",
        alignItems: "center",
        paddingTop: theme.spacing(2),
        paddingBototm: theme.spacing(2),
        paddingLeft: theme.spacing(2),
        paddingRight: theme.spacing(2)
    },
    contentBody: {
        flexGrow: 1,
        display: "flex",
        alignItems: "center",
        [theme.breakpoints.down("md")]: {
            justifyContent: "center"
        }
    },
    form: {
        paddingLeft: 100,
        paddingRight: 100,
        paddingBottom: 125,
        flexBasis: 700,
        [theme.breakpoints.down("sm")]: {
            paddingLeft: theme.spacing(2),
            paddingRight: theme.spacing(2)
        }
    },
    title: {
        marginTop: theme.spacing(3)
    },
    policy: {
        marginTop: theme.spacing(1),
        display: "flex",
        alignItems: "center"
    },
    policyCheckbox: {
        marginLeft: "-14px"
    },
    signUpButton: {
        margin: theme.spacing(2, 0)
    },
    show: {
        display: "block",
    },
    hide: {
        display: "none"
    }
}));
