import React, { useState }      from "react";
import { Link as RouterLink }   from "react-router-dom";
import { makeStyles }           from "@material-ui/styles";
import Input                    from "./Input";
import {
    Typography,
    Button,
    Link,
} from "@material-ui/core";

const defaultValue = {
    value: "",
    error: false,
}

const labels = {
    street: "Rua",
    number: "Número",
    complement: "Complemento",
    neighborhood: "Bairro",
    city: "Cidade",
    state: "Estado",
    country: "País",
}

const UserAddress = ({ onUserAddressConcluded, show }) => {
    const classes = useStyles();
    const [ isValid, updateIsValid ] = useState(true);
    const [ fields, updateFields ] = useState({
        street: defaultValue,
        number: defaultValue,
        complement: defaultValue,
        neighborhood: defaultValue,
        city: defaultValue,
        state: defaultValue,
        country: defaultValue,
    });

    const updateSpecificField = (field, data) => updateFields({
        ...fields,
        [field]: {
            ...fields[field],
            ...data
        }
    });

    const handleChange = ({ target: { name, value }}) => {
        updateIsValid(true);
        return updateSpecificField(name, { value, error: null });
    };

    const onSubmit = event => {
        event.preventDefault();

        for (let field in fields) {
            let { value } = fields[field];

            if (value.length === 0) {
                updateIsValid(false);
                return updateSpecificField(field, { error: "Preencha o campo corretamente" });
            }
        }

        return onUserAddressConcluded(fields);
    }

    return (
        <form className={`${classes.form} ${ show ? classes.show : classes.hide }`} onSubmit={ onSubmit }>
            <Typography className={classes.title} variant="h2">Falta mais um passo...</Typography>
            <Typography color="textSecondary" gutterBottom>Agora só precisamos do seu endereço</Typography>
            { function() {
                let inputs = [];

                for (let key in labels) {
                    inputs.push(
                        <Input
                            key={ key }
                            field={ key }
                            label={ labels[key] }
                            fields={ fields }
                            onChange={ handleChange }
                        />
                    )
                }

                return inputs;
            }()}
            <Button
                className={classes.signUpButton}
                color="primary"
                disabled={ !isValid }
                fullWidth
                size="large"
                type="submit"
                variant="contained"
            >
                Criar conta
            </Button>
            <Typography color="textSecondary" variant="body1">
                {"Já possui conta? "}
                <Link component={ RouterLink } to="/login" variant="h6">Faça login</Link>
            </Typography>
        </form>
    )
}

export default UserAddress;

const useStyles = makeStyles(theme => ({
    select: {
        marginTop: theme.spacing(2),
        minWidth: 120,
        width: "100%"
    },
    content: {
        height: "100%",
        display: "flex",
        flexDirection: "column",
        overflowY: "auto"
    },
    contentHeader: {
        display: "flex",
        alignItems: "center",
        paddingTop: theme.spacing(2),
        paddingBototm: theme.spacing(2),
        paddingLeft: theme.spacing(2),
        paddingRight: theme.spacing(2)
    },
    contentBody: {
        flexGrow: 1,
        display: "flex",
        alignItems: "center",
        [theme.breakpoints.down("md")]: {
            justifyContent: "center"
        }
    },
    form: {
        paddingLeft: 100,
        paddingRight: 100,
        paddingBottom: 125,
        flexBasis: 700,
        [theme.breakpoints.down("sm")]: {
            paddingLeft: theme.spacing(2),
            paddingRight: theme.spacing(2)
        }
    },
    title: {
        marginTop: theme.spacing(3)
    },
    policy: {
        marginTop: theme.spacing(1),
        display: "flex",
        alignItems: "center"
    },
    signUpButton: {
        margin: theme.spacing(2, 0)
    },
    show: {
        display: "block",
    },
    hide: {
        display: "none"
    }
}));
