import React, { useState } from "react";
import { connect }      from "react-redux";
import { newHelp }      from "actions/help";
import { makeStyles }   from "@material-ui/styles";
import { Loader }       from "components";
import * as Utils       from "helpers/utils";
import {
    Typography,
    TextField,
    Button,
    Grid,
} from "@material-ui/core";

const RequestHelp = ({ data: userID, newHelp }) => {
    const classes = useStyles();
    const [ description, setDescription ] = useState("");
    const [ loader, setLoader ] = useState(false);

    const onChange = e => setDescription(e.target.value);

    const sendHelp = () => {
        setLoader(true);

        return newHelp(userID, description, status => {
            if (!status)
                return Utils.notificationError("Não foi possível solicitar a ajuda desse voluntário no momento. Por favor, tente novamente.");

            Utils.closeModal();
            return Utils.notificationSuccess("Sua ajuda foi solicitada com sucesso!");
        });
    }

    return (
        <div className={ classes.paper }>
            <Grid container spacing={ 4 }>
                <Grid item xs={ 12 } style={{ marginBottom: -15 }}>
                    <Typography variant="h4">{ loader ? "Enviando sua solicitação de ajuda" : "Solicitar ajuda" } </Typography>
                </Grid>

                <Loader style={{ width: "100%", margin: "50px 0" }} active={ loader }/>

                {(!loader) && (
                    <>
                        <Grid item xs={ 12 }>
                            <TextField
                                fullWidth
                                multiline
                                label="Descrição do Pedido"
                                placeholder="Diga pra gente porque você precisa de ajuda..."
                                margin="dense"
                                name="description"
                                onChange={ onChange }
                                value={ description }
                                variant="outlined"
                                rows={ 7 }
                            />
                        </Grid>
                        <Grid item xs={ 12 } style={{ paddingTop: 0 }}>
                            <Button fullWidth onClick={ sendHelp } color="primary" variant="contained">Solicitar Ajuda</Button>
                        </Grid>
                    </>
                )}

            </Grid>
        </div>
    )
}

export default connect(null, { newHelp })(RequestHelp);

const useStyles = makeStyles((theme) => ({
    paper: {
        backgroundColor: theme.palette.background.paper,
        boxShadow: theme.shadows[5],
        padding: theme.spacing(4),
        borderRadius: 10,
        minWidth: 350,

        [theme.breakpoints.down("xs")]: {
            maxWidth: "90%",
        },
        [theme.breakpoints.up("sm")]: {
            maxWidth: 500,
        },
        [theme.breakpoints.up("md")]: {
            maxWidth: 500,
        },
        [theme.breakpoints.up("lg")]: {
            maxWidth: 450,
        },
    },
}));
